<template>
  <div class="text-light pd-2 flex-column flex-1" v-loading="loading">
    <table class="pure-table">
      <thead>
        <tr>
          <th>用户名</th>
          <th>标题</th>
          <th>得分</th>
          <th>总分</th>
          <th>用时</th>
          <th>时间限制</th>
          <th>时间</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>
            {{ result.username }}
          </td>
          <td>
            {{ result.title }}
          </td>
          <td>
            {{ result.score }}
          </td>
          <td>
            {{ result.totalScore }}
          </td>
          <td>
            {{ result.timeUsed | clock }}
          </td>
          <td>
            {{ result.timeLimit | clock }}
          </td>
          <td>
            {{ result.createdAt | formatDateTime }}
          </td>
        </tr>
      </tbody>
    </table>
    <h3>
      Details:
    </h3>
    <card
      v-for="(item, index) in result.details"
      :data="item"
      :index="index + 1"
      :key="index"
      />
  </div>
</template>

<script>
import Card from './components/card.vue';

export default {
  components: {
    Card,
  },
  data() {
    return {
      result: {
        details: [],
      },
      loading: true,
    };
  },
  mounted() {
    const { id } = this.$route.params;
    this.$axios.get(`${this.$url.results}/${id}`)
      .then((resp) => {
        this.result = resp.data;
        this.loading = false;
      })
      .catch(() => {
        this.loading = false;
      });
  },
};
</script>